import * as React from 'react'
import PartOfSpeech from './PartOfSpeech'
import TextInput from './TextInput'

export default function Categorizer() {
    const words = ['hello', 'world', '!']
    return (
    <div>dd
        <TextInput />
        {words.map((e) => <PartOfSpeech label={e}/>)} 
    </div>)
}